<template>
	<div class="page">
		<!-- 面包屑 -->
		<el-breadcrumb separator-class="el-icon-arrow-right" class="admin-breadcrumb">
			<el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item :to="{ path: '/problem' }">知识库</el-breadcrumb-item>
			<el-breadcrumb-item>{{title}}</el-breadcrumb-item>
		</el-breadcrumb>
		<!-- form -->
		<div class="headings">{{title}}</div>
		<div class="admin-form">
			<el-form ref="form" :model="form" label-width="80px">
				<el-form-item label="问题" prop="title" :rules="[{ required: true, message: '问题不能为空', trigger: 'blur' }]">
					<el-input v-model="form.title"></el-input>
				</el-form-item>
				<el-form-item label="类型" prop="type" :rules="[{ required: true, message: '请选择类型', trigger: 'change' }]">
					<el-select v-model="form.type" placeholder="请选择类型">
						<el-option label="纯内容回复" value="1"></el-option>
						<el-option label="外部链接" value="2"></el-option>
						<el-option label="内部连接" value="3"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="跳转地址">
					<el-input v-model="form.url"></el-input>
				</el-form-item>
				<el-form-item label="回复内容">
					<el-input type="textarea" v-model="form.reply"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="onSubmit" :loading="postLoading">提交</el-button>
					<el-button onclick="history.go(-1)">返回</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
export default {
  data() {
    return {
      title: this.$route.name,
      loading: false,
      postLoading: false,
      form: {
        title: "",
        reply: "",
        type: "",
        url: "",
        id: "",
        countID: ""
      }
    };
  },
  mounted: function() {
    // 判断编辑
    if (this.$route.query.id) {
      this.id = this.$route.query.id;
      this.getData(this.id);
    }
    // 判断添加问题删除无答复记录
    if (this.$route.query.countID) {
      this.form.countID = this.$route.query.countID;
      this.form.title = this.$route.query.countTitle;
    }
  },
  methods: {
    // 提交信息
    onSubmit() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          this.postLoading = true;
          this.$http
            .post(this.httpUrl + "/AnswerBank/Edit", this.form, {
              headers: this.postHeader
            })
            .then(response => {
              if (response.data.status == "y") {
                this.$message({
                  message: "提交成功，返回列表",
                  type: "success",
                  duration: 2000,
                  onClose: function() {
                    window.history.go(-1);
                  }
                });
              } else {
                this.$message({
                  message: response.data.info,
                  type: "warning",
                  duration: 2000
                });
                this.postLoading = false;
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 请求原信息
    getData(id) {
      this.$http
        .get(this.httpUrl + "/AnswerBank/Edit", { params: { id: id } })
        .then(response => {
          this.form = response.data;
          this.loading = false;
        });
    }
  }
};
</script>

<style>
</style>


